import React, { Component } from "react";
import { Grid, Image, Container, Button } from "semantic-ui-react";

export default class UserProfile extends Component {
  constructor(props) {
    super(props);

    this.state = {
      edit: false
    };
  }

  render() {
    return (
      <Grid>
        <Grid.Row columns={2}>
          <Grid.Column width={3}>
            <Image
              src={this.props.userData.profile_picture}
              size="small"
              circular
            />
          </Grid.Column>
          <Grid.Column width={8}>
            <Container>
              <h2>First Name: {this.props.userData.first_name}</h2>
              <h2>Last Name: {this.props.userData.last_name}</h2>
              <h2>Email: {this.props.userData.email}</h2>
              <br />
              <Button primary>Edit Profile</Button>
            </Container>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    );
  }
}
